@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600;700&display=swap");
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
  scroll-behavior: smooth;
  overflow: overlay;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  line-height: 1.5;
  background-color: #EEEEEE;
  display: flex;
  flex-direction: column;
  height: 100%;
}

body::-webkit-scrollbar {
  width: 0.7rem;
  opacity: 0;
}

body::-webkit-scrollbar-thumb {
  background-color: gray;
  border-radius: 0.5rem;
}

main {
  flex: 1;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  text-align: center;
}

h1 {
  font-weight: 600;
  font-size: 3.8rem;
}

h2 {
  font-weight: 400;
  font-size: 3.2rem;
}

h3 {
  font-size: 3rem;
}

.error {
  color: red;
}

.exito {
  color: green;
}
.exito p {
  margin-bottom: 0;
}

/** Media Queries **/
html {
  line-height: 1.15; /* 1 */
  -webkit-text-size-adjust: 100%; /* 2 */
}

/* Sections
   ========================================================================== */
/**
 * Remove the margin in all browsers.
