.contenedor_inicio {
  display: flex;
  flex-direction: column;
  height: 20rem;
  border-top: 3px solid #000000;
  border-bottom: 3px solid #000000;
}

.contenedor_texto {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.contenedor_texto h2 {
  font-size: 2.5rem;
}
.contenedor_texto p {
  font-size: 2rem;
  transition: all linear 0.2s;
}

@media (min-width: 768px) {
  .contenedor_inicio {
    display: grid;
    grid-template-columns: 60% auto;
  }
}
.imagen_inicio {
  color: #FFFFFF;
  background-image: url(../img/Fondo.webp);
  height: 55%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  transition: all ease-in-out 0.2s;
}
.imagen_inicio h2 {
  transition: all linear 0.2s;
}

@media (min-width: 768px) {
  .imagen_inicio {
    height: auto;
    align-items: flex-start;
    padding-left: 5rem;
  }
  .imagen_inicio h2, .imagen_inicio h1 {
    text-align: start;
  }
}
.informacion_inicio {
  background-color: #EEEEEE;
  padding: 1rem 3rem 9rem 3rem;
  text-align: center;
  height: 45%;
}

@media (min-width: 768px) {
  .informacion_inicio {
    height: 100%;
  }
}
.informacion_inicio:hover p {
  color: #5C0909;
}

.imagen_inicio:hover h2 {
  color: #d5d5d5;
}

@media (min-width: 768px) {
  .contenedor_texto h2 {
    font-size: 3.2rem;
  }
}
.flecha {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 0;
  margin: -2rem 0 2rem 0;
  justify-content: end;
  align-items: center;
  cursor: pointer;
  filter: invert(0);
}
.flecha p {
  margin: 0 0 1.2rem 0;
}
.flecha img {
  width: 6rem;
  height: 6rem;
}
.flecha img:hover {
  animation-name: moverse;
  animation-duration: 1s;
  animation-delay: 0.2s;
  animation-iteration-count: infinite;
}

@media (min-width: 768px) {
  .flecha {
    filter: invert(100);
  }
}
.flecha-contenido {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  color: black;
}

header {
  background-color: black;
}

.contenedor_navegacion {
  display: flex;
  flex-direction: row;
  color: #FFFFFF;
  align-items: center;
  justify-content: center;
  height: 7rem;
}
.contenedor_navegacion a {
  text-decoration: none;
  color: #FFFFFF;
}

@media (min-width: 1024px) {
  .contenedor_navegacion {
    justify-content: space-evenly;
  }
}
.navegacion {
  display: none;
  visibility: hidden;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  width: 60%;
}
.navegacion a {
  margin: 0;
  transition: all ease-in-out 0.2s;
}
.navegacion a:hover {
  color: #d5d5d5;
}

.contenedor-navegacion-completa {
  display: flex;
  justify-content: end;
}

.mostrar {
  display: flex;
}

.navegacion-completa {
  display: none;
  flex-direction: column;
  align-items: end;
  visibility: visible;
  background-color: #111010;
  padding: 6rem 8rem;
  position: fixed;
  width: 0;
  z-index: 1;
  text-align: end;
}
.navegacion-completa a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  border: 1px solid transparent;
  margin-bottom: 0.5rem;
  transition: all linear 0.2s;
}
.navegacion-completa p {
  color: #bbbbbb;
  font-size: 1.5rem;
  border: 1px solid transparent;
  transition: all linear 0.2s;
}
.navegacion-completa a:hover, .navegacion-completa p:hover {
  border-bottom: 1px solid #FFFFFF;
}
.navegacion-completa h3 {
  margin: 0;
  color: #FFFFFF;
}
.navegacion-completa .nav-com-btn {
  margin: 2rem 0 0 0;
}
.navegacion-completa .nav-com-btn a {
  font-size: 2.5rem;
}
.navegacion-completa .parrafo {
  border: none;
}
.navegacion-completa .parrafo:hover {
  border: none;
}

.overlay {
  background-color: rgba(0, 0, 0, 0.5);
  bottom: 0;
  display: grid;
  height: 100%;
  left: 0;
  place-items: center;
  position: fixed;
  right: 0;
  top: 0;
  width: 100%;
}

.mostrar {
  display: flex;
  animation-name: aparecer;
  animation-duration: 0.2s;
}

@media (max-width: 768px) {
  .navegacion-completa {
    width: 70%;
  }
}
@media (max-width: 480px) {
  .navegacion-completa {
    align-items: center;
    padding: 7rem 2rem;
    text-align: center;
    width: 100%;
  }
}
.texto_navegacion {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  margin-right: 2rem;
}
.texto_navegacion a {
  text-decoration: none;
  background-color: #450707;
  font-size: 2rem;
  padding: 0.7rem;
  border-radius: 1rem;
  border: 2px solid transparent;
  color: #FFFFFF;
  transition: all linear 0.2s;
}
.texto_navegacion a:hover {
  background-color: transparent;
  border: 2px solid #450707;
}

@media (min-width: 1024px) {
  .texto_navegacion {
    width: 40%;
    margin: 0;
  }
}
@media (min-width: 768px) {
  .contenedor_navegacion {
    flex-direction: row;
  }
  .navegacion {
    display: flex;
    flex-direction: row;
    visibility: visible;
  }
  .navegacion a:not(:last-child) {
    margin-right: 5rem;
  }
}
.barras {
  display: flex;
  flex-direction: column;
  margin-left: 1rem;
  cursor: pointer;
}
.barras img {
  filter: invert(100%);
}

.hamburguesa {
  filter: invert(100);
  width: 3.5rem;
}

.barra {
  height: 0.2rem;
  background-color: #FFFFFF;
  width: 2.5rem;
}

.barra:not(:last-child) {
  margin-bottom: 0.5rem;
}

.barras-nav {
  display: flex;
}

@media (min-width: 1024px) {
  .barras-nav {
    display: none;
  }
}
@keyframes aparecer {
  0% {
    width: 0%;
  }
  50% {
    width: 50%;
  }
}
.contenedor {
  margin: 0;
}
.contenedor h2 {
  padding-top: 2rem;
}

.contenedor-grid2 {
  display: flex;
  flex-direction: column;
  background-color: #EEEEEE;
  text-align: center;
}
.contenedor-grid2 p {
  font-size: 1.8rem;
}
.contenedor-grid2 h2 {
  font-weight: 600;
}
.contenedor-grid2 h4 {
  font-weight: 400;
}

.areas {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

@media (min-width: 768px) {
  .contenedor-grid2 {
    display: grid;
    grid-template-columns: 60% auto;
  }
}
.contenedor-areas {
  padding: 0 3rem;
}

.area {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  width: 60%;
  margin: 0.5rem;
}
.area h4 {
  font-size: 2rem;
}
.area p {
  font-size: 2rem;
}
.area img {
  width: 5rem;
  height: 7rem;
}
.area h3 {
  padding: 3rem;
  margin: 0;
}

.area:not(:last-child) {
  border-bottom: 3px solid #000000;
}

.tegnologias {
  display: flex;
  flex-direction: column;
  text-align: center;
  width: 100%;
}

.tegnologia {
  width: 100%;
  border-radius: 1rem;
  margin-bottom: 1rem;
}
.tegnologia h4 {
  margin: 0;
}

@media (min-width: 768px) {
  .tegnologias {
    text-align: right;
  }
  .area {
    flex-direction: row;
    text-align: left;
  }
}
.informacion-mi {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #000000;
  color: #FFFFFF;
  padding: 0 5rem;
}
.informacion-mi p {
  font-size: 2rem;
}
.informacion-mi img {
  margin-top: 2rem;
  filter: invert(100);
  rotate: 180deg;
  width: 4rem;
  height: 4rem;
}
.informacion-mi img:hover {
  animation-name: moverse;
  animation-duration: 1s;
  animation-delay: 0.2s;
  animation-iteration-count: infinite;
}
.informacion-mi a {
  text-decoration: none;
  color: #FFFFFF;
  margin: 0.5rem;
  font-size: 2rem;
  transition: all linear 0.1s;
}
.informacion-mi a:hover {
  color: #EEEEEE;
}

@keyframes moverse {
  0% {
    transform: translate(0, 0);
  }
  50% {
    transform: translate(0, -1rem);
  }
  100% {
    transform: translate(0, 0);
  }
}
.portafolio {
  display: flex;
  flex-direction: column;
  padding: 2rem;
}

.proyecto {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.slider {
  display: flex;
}
.slider img {
  width: 100%;
}

.hide {
  display: none;
}

.formulario-grid {
  grid-template-columns: repeat(2, 1fr);
  height: 100%;
}

.formulario-texto {
  background-color: #111010;
  color: #FFFFFF;
  padding: 2rem 10rem;
}

.contenedor-formulario, .formulario-texto {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.contenedor-formulario h2, .formulario-texto h2 {
  margin: 0;
}

.formulario {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 5rem;
  width: 100%;
}
.formulario label {
  font-weight: 600;
  font-size: 2rem;
}
.formulario input {
  border: none;
  border-bottom: 2px solid #000000;
  background-color: transparent;
  font-size: 1.8rem;
  margin-bottom: 2.5rem;
}
.formulario textarea {
  height: 15rem;
}
.formulario input, .formulario textarea {
  width: 70%;
  padding: 0.5rem;
}
.formulario .btn {
  width: 10rem;
  background-color: #5C0909;
  border: 2px solid transparent;
  margin-top: 3rem;
  font-size: 2rem;
  border-radius: 2rem;
  color: #FFFFFF;
  transition: all ease-in-out 0.5s;
}
.formulario .btn:hover {
  border: 2px solid #5C0909;
  background-color: transparent;
  color: #000000;
}